select {
	display: none;
}
.nice-select {
	-webkit-tap-highlight-color: transparent;
	background-color: $background_color_1;
	border-radius: 5px;
	border: solid 1px #e8e8e8;
	box-sizing: border-box;
	clear: both;
	cursor: pointer;
	display: block;
	float: left;
	font-family: $font_family_1;
	font-size: 14px;
	font-weight: normal;
	height: 42px;
	line-height: 40px;
	outline: none;
	padding-left: 18px;
	padding-right: 30px;
	position: relative;
	text-align: left !important;
	transition: all 0.2s ease-in-out;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	white-space: nowrap;
	width: auto;
	&:hover {
		border-color: $border_color_1;
	}
	&:active {
		border-color: $border_color_2;
	}
	&:focus {
		border-color: $border_color_2;
	}
	&:after {
		border-bottom: 2px solid #999;
		border-right: 2px solid #999;
		content: '';
		display: block;
		height: 5px;
		margin-top: -4px;
		pointer-events: none;
		position: absolute;
		right: 12px;
		top: 50%;
		-webkit-transform-origin: 66% 66%;
		transform-origin: 66% 66%;
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
		transition: all 0.15s ease-in-out;
		width: 5px;
	}
	.list {
		background-color: $background_color_1;
		border-radius: 5px;
		box-shadow: 0 0 0 1px rgba(68, 68, 68, 0.11);
		box-sizing: border-box;
		margin-top: 4px;
		opacity: 0;
		overflow: hidden;
		padding: 0;
		pointer-events: none;
		position: absolute;
		top: 100%;
		left: 0;
		-webkit-transform-origin: 50% 0;
		transform-origin: 50% 0;
		-webkit-transform: scale(0.75) translateY(-21px);
		transform: scale(0.75) translateY(-21px);
		transition: all 0.2s cubic-bezier(0.5, 0, 0, 1.25), opacity 0.15s ease-out;
		z-index: 9;
		&:hover {
			.option {
				&:not(:hover) {
					background-color: $background_color_2 !important;
				}
			}
		}
	}
	.option {
		cursor: pointer;
		font-weight: 400;
		line-height: 40px;
		list-style: none;
		min-height: 40px;
		outline: none;
		padding-left: 18px;
		padding-right: 29px;
		text-align: left;
		transition: all 0.2s;
		&:hover {
			background-color: $background_color_3;
		}
	}
	.option.focus {
		background-color: $background_color_3;
	}
	.option.selected.focus {
		background-color: $background_color_3;
	}
	.option.selected {
		font-weight: bold;
	}
}
.nice-select.open {
	border-color: $border_color_2;
	&:after {
		-webkit-transform: rotate(-135deg);
		transform: rotate(-135deg);
	}
	.list {
		opacity: 1;
		pointer-events: auto;
		-webkit-transform: scale(1) translateY(0);
		transform: scale(1) translateY(0);
	}
}
.nice-select.disabled {
	border-color: $border_color_3;
	color: $color_1;
	pointer-events: none;
	&:after {
		border-color: $border_color_4;
	}
}
.nice-select.wide {
	width: 100%;
	.list {
		left: 0 !important;
		right: 0 !important;
	}
}
.nice-select.right {
	float: right;
	.list {
		left: auto;
		right: 0;
	}
}
.nice-select.small {
	font-size: 12px;
	height: 36px;
	line-height: 34px;
	&:after {
		height: 4px;
		width: 4px;
	}
	.option {
		line-height: 34px;
		min-height: 34px;
	}
}
